import Vue from 'vue';

export default {
	data() {
		return {
			loading: false,
			curId: 0,
			activeClass: -1,
			indexArr: {},
			godid: '',
			hotmain: [],
			indicator: true, //是否显示指示点
			interval: 5000, //自动切换时间间隔
			duration: 400, //滑动动画时长
			autoplay: true, //是否自动切换
			circular: true, //是否采用衔接滑动
			classon: false, //判断模态框
			gghurl: false,
			cur: 0, //当前所在滑块的index
			page: 1,
			hide: true,
			classoninfo: '', //加载展示内容
			hasno: false, //判断有没有消息
			success: false
		};
	},
	onReachBottom() {
		this.scroll();
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			// 来自页面内分享按钮
			console.log(res.target);
		}
		return {
			title: 'ABCbook国际亲子阅读',
			path: '/pages/index/index'
		};
	},
	onPullDownRefresh: function() {
		this.load();
		setTimeout(function() {
			uni.stopPullDownRefresh(); //停止下拉刷新动画
		}, 1000);
	},
	// 实时获取滚动的值，到一定位置显示返回顶部
	onPageScroll: function(Object) {
		if (Object.scrollTop > 800) {
			this.hide = false;
		} else {
			this.hide = true;
		}
	},
	onReady() {
		// uni.setStorageSync('voivegodid','')
		console.log('onReady');
		// console.log(uni.getStorageSync('voivegodid'))
		// this.godid=uni.getStorageSync('voivegodid')
	},
	onUnload() {
		if (this.timer) {
			clearInterval(this.timer);
			this.timer = null;
		}
	},
	onShow() {
		console.log('onShow');
		// this.godid = uni.getStorageInfo('voivegodid')
		this.timer = setInterval(() => {
			this.godid = uni.getStorageSync('voivegodid');
			// console.log(this.godid)
		}, 500);
		// console.log(uni.getStorageInfo('voivegodid'))
		// this.load()
	},
	onUnload() {
		if (this.timer) {
			clearInterval(this.timer);
			this.timer = null;
		}
	},
	onLoad: function(options) {
		// uni.setStorageSync('voivegodid','')
		uni.setStorageSync('uid', options.u_id);
		this.load();
	},
	methods: {
		// 页面初始化
		load() {
			var that = this;
			this.$api.quest('index', {}, res => {
				console.log(res);
				that.hotmain = res.data.data.goods_list;
				that.indexArr = res.data.data;
				this.loading = true;
			});
			this.$api.quest('user/hasNoReadMessage', {}, res => {
				if (res.data.data == 1) {
					this.hasno = true;
				}
			});
		}
	}
};
